<template>
  <div>
    <el-button @click="handleAdd"></el-button>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="date" label="" width="180"> </el-table-column>
      <el-table-column prop="name" label="" width="180"> </el-table-column>
      <el-table-column prop="six" label="" width="180"> </el-table-column>
      <el-table-column prop="address" label=""> </el-table-column>
      <el-table-column prop="handle" label="">
        <template slot-scope="scope">
          <el-button type="text" @click="handleEdit(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" label-suffix=":" ref="form" :rules="rules">
        <el-form-item label="" prop="name" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="date" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.date"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder=""
          >
          </el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="six" :label-width="formLabelWidth">
          <el-select v-model="form.six" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="address" :label-width="formLabelWidth">
          <el-input
            v-model="form.address"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4 }"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('form')"> </el-button>
        <el-button type="primary" @click="submitForm('form')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        name: "",
        date: "",
        six: "",
        address: ""
      },
      formLabelWidth: "120px",
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          six: "1",
          address: " 1518 "
        },
        {
          date: "2016-05-02",
          name: "",
          six: 0,
          address: " 1518 "
        },
        {
          date: "2016-05-02",
          name: "",
          six: "1",
          address: " 1518 "
        },
        {
          date: "2016-05-02",
          name: "",
          six: "1",
          address: " 1518 "
        }
      ],
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        six: [{ required: true, message: "", trigger: "change" }],
        date: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        address: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    handleEdit(row) {
      this.form = Object.assign({}, row);
      // setTimeout(() => {
      //   this.dialogFormVisible = true;
      // }, 500);
      this.dialogFormVisible = true;
      console.log(row);
    },
    handleAdd() {
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.resetForm("form");
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="less" scoped></style>



